




























































	import loadMore from '../../../components/load-more.vue'
	export default {
		data() {
			return {
				type:'0',
				coupon:'1',//使用优惠券
				discount:'1',//折扣券
				list: [],
				loadingType: 0,
				contentText: {
					contentdown: "上拉显示更多",
					contentrefresh: "正在加载...",
					contentnomore: "没有更多数据了"
				}
			};
		},
		onLoad() {
			let list = [];
			for (let i = 1; i < 11; i++) {
				list.push(i);
			}
			this.list = list;
		},
		methods: {
			selectTab(e) {
				this.type = e.currentTarget.dataset.type;
				// var tyep1 =this.type
// 				if(this.type==2){
// 					this.type=1;
// 				}
				console.log(this.type)
				uni.showNavigationBarLoading() //在标题栏中显示加载
				//模拟加载
				setTimeout(() => {
					uni.hideNavigationBarLoading() //完成停止加载
				}, 1000);
			}
		},
		onReachBottom() {
			if (this.loadingType !== 0) {
				return;
			}
			this.loadingType = 1;
			let list = [],
				maxItem = this.list[this.list.length - 1],
				length = maxItem + 5;
			for (let i = maxItem + 1; i < length; i++) {
				list.push(i);
			}
			setTimeout(() => {
				if (length > 26) {
					this.loadingType = 2;
					return;
				}
				this.list = this.list.concat(list);
				this.loadingType = 0;
			}, 800);
		},
		components: {
			loadMore
		}
	}



